
import XHeader from 'vux/src/components/x-header/index.vue'

export default{
name:"Information",
components:{
XHeader
},
data(){

return {
id:this.$store.state.listId,
content:""
}
},
mounted(){

var _this=this
this.$http.get("https://www.xueguoguo.cn/wxapi/Article?funcid=info&id="+_this.id,{}).then(function(result){
console.log(result.data)
_this.content=result.data.result[0].content
})

},methods:{
back(){
this.$router.go(-1);
this.$router.isBack = true;
}
}
}



