
import XHeader from 'vux/src/components/x-header/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
name: "Allschool",
components: {
XHeader,
LoadMore,
Scroller,
Loading,
Spinner,
Divider
},
data() {
return {
SchoolArr: [],
index: 0,

pullupConfig2: {
content: '上拉加载更多',
downContent: '松开进行加载',
upContent: '上拉加载更多',
loadingContent: '加载中...'
},
pulldownConfig2: {
content: '下拉刷新',
downContent: '下拉刷新',
upContent: '释放刷新',
loadingContent: '加载中...'
},

noData: false,
noDataFlag: false,
status1: {
pulldownStatus: 'default'
}

}
},
mounted() {

this.getData();

this.$nextTick(() => {
this.$refs.demo2.reset()
})

},
methods: {
back() {
this.$router.go(-1);
this.$router.isBack = true;
},
search() {
this.$router.push({
path: '/SearchSchool'
})
},
load1() {
setTimeout(() => {

this.status1.pulldownStatus = 'default'
}, 2000)
},

load2() {
setTimeout(() => {
this.index += 1
this.getData()
setTimeout(() => {
this.$refs.demo2.donePullup()
}, 100)
if(this.noDataFlag) { // unload plugin
setTimeout(() => {
this.noData = true
this.$refs.demo2.disablePullup()

}, 100)
}
}, 2000)
},
getData() {
var _this = this;
this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/query?token=9C329745-E728-46CF-B4B0-D7A1D2119202&index=" + this.index + "&size=10&" + this.$store.state.SchoolSearchType + "=" + this.$store.state.SchoolSearch, {})
.then(function(result) {

if(result.data.data.length == 0) {
_this.noDataFlag = true;
return;
}

for(var i = 0; i < result.data.data.length; i++) {
if(result.data.data[i].luqu == "-") {
result.data.data[i].luqu = "暂无数据"
}
_this.SchoolArr.push(result.data.data[i])
}

})
}
},
activated() {
this.$refs.demo2.reset()
}
}



