

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import Vuex from 'vuex';
import HelloFromVux from './components/HelloFromVux'
import Home from './components/Home'
import Mark from './components/Mark'
import Wish from './components/Wish'
import Professional from './components/Professional'
import Occupation from './components/Occupation'
import Analog from './components/Analog'
import School from './components/School'
import SchoolSelect from './components/SchoolSelect'
import ProSelect from './components/ProSelect'
import Allschool from './components/Allschool'
import Information from './components/Information'
import MorePhoto from './components/MorePhoto'
import SelectPro from './components/SelectPro'
import SearchPro from './components/SearchPro'
import SearchSchool from './components/SearchSchool'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'

Vue.use(AjaxPlugin)
Vue.use(Vuex);
Vue.use(VueRouter)
const myCity = new BMap.Geolocation();



const  vuex_store = new Vuex.Store({
state:{
id:"",
ProName:"",
SchoolIndex:0,
listId:0,
ProSearch:"",
SchoolSearch:"",
SchoolSearchType:""


},
mutations:{

}
})
myCity.getCurrentPosition(myFun);
function myFun(GeolocationResult){
var index=GeolocationResult.address.province.indexOf("省")

vuex_store.state.address=GeolocationResult.address.province.substr(0,index)


}
Vue.prototype.message={
mark:"",
ranking:"",
fromPlace:"",
subject:"",
school:"",
sex:"",
pro:[],
occ:[]
};
const routes = [{
path: '/',
component: HelloFromVux,
children:[{
path: '',
name: 'Home',
component: Home
}
,
{
path:'/Mark',
name:'Mark',
component:Mark
},
{
path:'/Wish',
name:'Wish',
component:Wish
},
{
path:'/Professional',
name:'Professional',
component:Professional
},{
path:'/Occupation',
name:'Occupation',
component:Occupation
},
{
path:'/Analog',
name:'Analog',
component:Analog
},
{
path:'/School',
name:'School',
component:School
},
{
path:'/SchoolSelect',
name:'SchoolSelect',
component:SchoolSelect
},
{
path:'/ProSelect',
name:'ProSelect',
component:ProSelect
}
,
{
path:'/Allschool',
name:'Allschool',
component:Allschool
},
{
path:'/Information',
name:'Information',
component:Information
},{
path:'/MorePhoto',
name:'MorePhoto',
component:MorePhoto
},{
path:'/SelectPro',
name:'SelectPro',
component:SelectPro
},{
path:'/SearchPro',
name:'SearchPro',
component:SearchPro
},{
path:'/SearchSchool',
name:'SearchSchool',
component:SearchSchool
}

]
}]

const router = new VueRouter({
routes
})

FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
store:vuex_store,
render: h => h(App)
}).$mount('#app-box')



