
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
name: "Professional",
components: {
XHeader,
Toast,
Confirm,
Tabbar,
TabbarItem
},
data: function() {
return {
Proarr: ["化学类", "交通输入类", "交通输入类", "化学类", "交通输入类", "交通输入类", "化学类", "交通输入类", "交通输入类", "化学类", "交通输入类", "交通输入类", "化学类", "交通输入类", "交通输入类", "化学类", "交通输入类", "交通输入类", "化学类", "交通输入类", "交通输入类", "化学类", "交通输入类", "交通输入类", "化学类", "交通输入类", "交通输入类", "化学类"],
pcount: this.message.pro.length,
Pro: false,
leave: false,
comfirmBtn: false,
comfirmFlag: false
}
},
mounted() {
$('.weui-dialog').css("height", "3rem")
this.flagArr = this.flag()
this.background();
this.getHeight()
},

methods: {
back() {
if(this.comfirmFlag) {
this.$router.go(-1);
this.$router.isBack = true;
} else {
this.leave = true
}

},
onLeave() {
this.$router.go(-1);
this.$router.isBack = true;
},
select(index) {

if(this.flagArr[index]) {
$('.checkbox').eq(index).css({
"background": "#2B94FA",
"color": "#FFFFFF"
})
this.flagArr[index] = false;
var newobj = {};
newobj.index = index;
this.message.pro.push(newobj);
this.pcount = this.message.pro.length;
console.log(this.message.pro)
} else {
$('.checkbox').eq(index).css({
"background": "#FFFFFF",
"color": "#2B94FA"
})
this.flagArr[index] = true
for(var i = 0; i < this.message.pro.length; i++) {
if(this.message.pro[i].index == index) {
console.log(this.message.pro[i])
this.message.pro.splice(i, 1)
}
}
console.log(this.message.pro)
this.pcount = this.message.pro.length
}
},
flag() {

var len = this.Proarr.length;
var flagArr = [];
for(var i = 0; i < len; i++) {
flagArr[i] = true;
}
return flagArr;
},
cancel() {
this.Pro = true

},
background() {
for(var i = 0; i < this.message.pro.length; i++) {
$('.checkbox').eq(this.message.pro[i].index).css({
"background": "#2B94FA",
"color": "#FFFFFF"
});
this.flagArr[this.message.pro[i].index] = false;
}
},
comfirm() {
this.comfirmBtn = true
},
onComfirm() {
this.comfirmBtn = false;
this.comfirmFlag = true;
this.back()
},
getHeight() {
console.log(document.documentElement.clientHeight)
var Height = document.documentElement.clientHeight * 2 / 100 + "rem"
$('.big_box').css("min-height", Height);

},
onDelete() {
$('.checkbox').each(function(i) {
$('.checkbox').eq(i).css({
"background": "#FFFFFF",
"color": "#2B94FA"
});
});
this.message.pro.length = 0;
this.pcount = this.message.pro.length;
for(var i = 0; i < this.flagArr.length; i++) {
this.flagArr[i] = true
}

}
}
}



