mounted(){
$(".vux-search-box").css("width","5.8rem");

this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/query?token=9C329745-E728-46CF-B4B0-D7A1D2119202",{}).then(function(result){
console.log(result.data)
})

console.log(this.$store.state.address)
},
