})
},
getResult(val) {
console.log(val)

},
onFocus() {
console.log('on focus')
},
onCancel() {
console.log('on cancel')
},
onBlur() {
console.log(1)
},
position(){
if(this.positionFlag){
$(".postion").animate({"height":"5.04rem"})
$(".position_img").animate({"transform":"rotate(180deg)"})
this.positionFlag=false
}else{
$(".postion").animate({"height":"2.16rem"})
$(".position_img").animate({'transform':"rotate(0deg)"})
this.positionFlag=true
}

},
pro(){
if(this.proFlag){
$(".pro").animate({"height":"6.48rem"})
$(".pro_img").animate({"transform":"rotate(180deg)"})
this.proFlag=false
}else{
$(".pro").animate({"height":"2.16rem"})
$(".pro_img").animate({"transform":"rotate(0deg)"})
this.proFlag=true
}
},
province(){
if(this.$store.state.address==undefined){
$('.weui-dialog').css("height","3rem")
this.Alert=true
}else{
this.$store.state.SchoolSearch=this.$store.state.address;
this.$store.state.SchoolSearchType="province";
this.$router.push({
path: '/Allschool'
})
console.log(this.$store.state.address)
}
}
}

}



