
import Zepto from '../../static/zepto.min.js'
import Progress from '../../static/progress.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

const list = () => ['可冲击 (0)', '较稳妥 (2)', '可保底 (0)']
let Analog_swiper;
export default {
directives: {
TransferDom
},
name: "Analog",
components: {
XHeader,
Tab,
TabItem,
Swiper,
SwiperItem,
AlertModule,
Alert
},
data: function() {
return {
mark: this.message.mark,
ranking: this.message.ranking,
fromPlace: this.message.fromPlace,
subject: this.message.subject,
ProLen: this.message.pro.length,
OccLen: this.message.occ.length,
school: this.message.school,

SchoolArr: ["华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学"],
SchoolArr1: ["华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学"],
SchoolArr2: ["华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学", "华南理工大学"],
scrollTop: false,
id: "001",
index: 0,
demo2: '可冲击 (0)',
list2: list(),
Alert: false
}
},
mounted() {

this.draw();
this.changeWidth();

console.log(this.$refs.swiper)

this.$nextTick(function() {
var height = Math.round(2.57 * this.SchoolArr.length) + 0.5 + "rem"
this.$refs.swiper.xheight = height
})

console.log(this.$refs.swiper)

window.addEventListener('scroll', this.handleScroll);
console.log()

},
methods: {
drap() {
console.log(this)
},
draw() {
$(".myChart").each(function() {
$(this).ringChart();
})
},

back() {
this.$router.go(-1);
this.$router.isBack = true;
},
toMark() {
this.$router.push({
path: '/Mark'
})
},
toWish() {
this.$router.push({
path: '/Wish'
})
},
prompt() {
$(".weui-dialog").css({
"height": "3.6rem",
"width": "5rem"
})
this.Alert = true
},

goSchool() {

this.$store.state.id = this.id
this.$router.push({
path: '/School'
})
},
changeWidth() {
if(this.ProLen + this.OccLen > 10) {
console.log(this.ProLen + this.OccLen)
$("#wish").css("width", "2.8rem")
}
},
change(currentIndex) {
if(currentIndex == 1) {
this.$nextTick(function() {
var height = Math.round(2.57 * this.SchoolArr2.length) + 0.1 + "rem"
this.$refs.swiper.xheight = height
})
} else if(currentIndex == 2) {
this.$nextTick(function() {
var height = Math.round(2.57 * this.SchoolArr1.length) + 0.1 + "rem"
this.$refs.swiper.xheight = height
})
} else {
this.$nextTick(function() {
var height = Math.round(2.57 * this.SchoolArr.length) + 0.5 + "rem"
this.$refs.swiper.xheight = height
})
}
},
handleScroll(e) {

console.log(1)
var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
var offsetTop = document.querySelector('#searchBar').offsetTop;
if(scrollTop > offsetTop) {
this.scrollTop = true
} else {
this.scrollTop = false
}
}

}
,destroyed(){
document.documentElement.scrollTop=0
window.removeEventListener("scroll",this.handleScroll)
}
}



