

var height=document.documentElement.clientHeight*2/100+"rem";

import XHeader from 'vux/src/components/x-header/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Zepto from '../../static/zepto.min.js'


export default{
directives: {
TransferDom
},
name:'Mark',
components:{
XHeader,
Picker,
Cell,
Toast,
Confirm,
Loading

},
data:function(){
return {
flag:"true",
chooseData:["高考分数","全省排行"],
data:"分数",
mark:this.message.mark,
ranking:this.message.ranking,
fromPlace:this.message.fromPlace,
subject:this.message.subject,
slots:[['广东', '北京', '上海', '天津', '湖南']],
maskStyle:{
height:height
},
sub:[["文科","理科"]],
transfer:{

subject:[""],
fromPlace:[""]
},
position: 'default',
showPositionValue:false,
show3:false,
textTitle:"",

showErrormark:false,
showErrorranking:false,
showLoad:false



}
},
mounted(){
if(this.message.fromPlace==""){
console.log()
this.transfer.fromPlace[0]=this.slots[0][0]
}else{
this.transfer.fromPlace[0]=this.message.fromPlace
}
if(this.message.subject==""){
this.transfer.subject[0]=this.sub[0][0]
}else{
this.transfer.subject[0]=this.message.subject
}

},


methods:{
onConfirm(value){
if(this.data=="分数"){

if(parseInt(value)>750||parseInt(value)<0){
this.showErrormark=true;

}else{
this.message.mark=value;
this.mark=this.message.mark
}


}else{

if(parseInt(value)<0){
this.showErrorranking=true;

}else if(parseInt(value)>=10000){
this.message.ranking=">9999"
this.ranking=this.message.ranking
}else{
this.message.ranking=value;

this.ranking=this.message.ranking
}



}

},
back (){
this.$router.go(-1);
this.$router.isBack = true;
},
placeShow(){
$("#place").fadeIn()
},
subShow(){
$("#subject").fadeIn()
},
choose(){
if(this.flag){
$(".select_btn").animate({"height":"1.41rem"})
$(".pull_down").animate({"transform":"rotate(180deg)"})
}else{
$(".select_btn").animate({"height":"0.7rem"});
$(".pull_down").animate({"transform":"rotate(0deg)"})
}

this.flag=!this.flag
}
,
choose_type(index,data){

if(this.flag==false){
if(index==1){
var temp=$('.select_btn li').eq(0).html();
$('.select_btn li').eq(0).html($('.select_btn li').eq(1).html());
$('.select_btn li').eq(1).html(temp);

if(temp=="高考分数"){
$(".mark_box span").html("名");
this.data="名次"

}else{
$(".mark_box span").html("分");
this.data="分数"
}
}

}


}
,
openMessage(){
$(".weui-toast").css("height","1rem")
this.show3=true
if(this.data=="分数"){
this.textTitle="请输入您的高考分数"

}else{
this.textTitle="请输入您的高考排名"

}

},


placeChange(value){
console.log(value)
this.transfer.fromPlace[0]=value[0];
},
subChange(value){

this.transfer.subject[0]=value[0];
},
closePlace(){
$('#place').fadeOut()
},
comfirmPlace(){
this.message.fromPlace=this.transfer.fromPlace[0]
this.fromPlace=this.message.fromPlace;
$('#place').fadeOut()
}
,
closeSub(){
$('#subject').fadeOut()
},
comfirmSub(){
this.message.subject=this.transfer.subject[0]
this.subject=this.message.subject;
$('#subject').fadeOut()
},
comfirm(position){
$(".weui-toast").css("height","1rem")
if(this.message.mark==""){
console.log(1)
this.position = position
this.showPositionValue=true
return;
}
else if(this.message.subject==""){
this.position = position
this.showPositionValue=true
return;
}
else if(this.message.fromPlace==""){
this.position = position
this.showPositionValue=true
return;
}
else if(this.message.ranking==""){
this.position = position
this.showPositionValue=true
return;
}else{
$(".weui-toast").css("min-height","2.5rem")
this.showLoad=true;
var _this=this
setTimeout(function(){
_this.showLoad=false;
_this.$router.push({path:'/Wish'})
},800)



}

}
}
}




