
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
name: 'Occupation',
components: {
XHeader,
Toast,
Confirm,
Tabbar,
TabbarItem
},
data: function() {
return {
Occarr: ["交通输入类", "交通输入类", "化学类", "交通输入类", "交通输入类", "化学类", "交通输入类", "交通输入类", "化学类"],
pcount: this.message.occ.length,
Occ: false,
leave:false,
comfirmBtn:false,
comfirmFlag:false
}
},
mounted() {
$('.weui-dialog').css("height", "3rem")
this.flagArr = this.flag();
this.background();
this.getHeight();
},

methods: {
back() {
if(this.comfirmFlag){
this.$router.go(-1);
this.$router.isBack = true;
}else{
this.leave=true
}

},
onLeave(){
this.$router.go(-1);
this.$router.isBack = true;
},
select(index) {

if(this.flagArr[index]) {
$('.checkbox').eq(index).css({
"background": "#2B94FA",
"color": "#FFFFFF"
})
this.flagArr[index] = false;
var newobj = {};
newobj.index = index;
this.message.occ.push(newobj);
this.pcount = this.message.occ.length;
console.log(this.message.occ)
} else {
$('.checkbox').eq(index).css({
"background": "#FFFFFF",
"color": "#2B94FA"
})
this.flagArr[index] = true
for(var i = 0; i < this.message.occ.length; i++) {
if(this.message.occ[i].index == index) {
console.log(this.message.occ[i])
this.message.occ.splice(i, 1)
}
}
console.log(this.message.occ)
this.pcount = this.message.occ.length
}
},
flag() {

var len = this.Occarr.length;
var flagArr = [];
for(var i = 0; i < len; i++) {
flagArr[i] = true;
}
return flagArr;
},
cancel() {

this.Occ = true

},
background() {
for(var i = 0; i < this.message.occ.length; i++) {
$('.checkbox').eq(this.message.occ[i].index).css({
"background": "#2B94FA",
"color": "#FFFFFF"
});
this.flagArr[this.message.occ[i].index] = false;
}
},
comfirm() {
console.log(1)
this.comfirmBtn=true


},
onComfirm(){
this.comfirmBtn=false;
this.comfirmFlag=true;
this.back()
},
getHeight() {
console.log(document.documentElement.clientHeight)
var Height = document.documentElement.clientHeight * 2 / 100 + "rem"
$('.big_box').css("min-height", Height)
},
onDelete() {
$('.checkbox').each(function(i) {
$('.checkbox').eq(i).css({
"background": "#FFFFFF",
"color": "#2B94FA"
});
});
this.message.occ.length = 0;
this.pcount = this.message.occ.length;
for(var i = 0; i < this.flagArr.length; i++) {
this.flagArr[i] = true
}

}
}
}



