
import XHeader from 'vux/src/components/x-header/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'

export default{
name:"MorePhoto",
components:{
XHeader,
XImg
},
data(){
return{
imgArr:[]
}
},
mounted(){
var _this=this
this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/brief/1",{}).then(function(result){
console.log(result.data.data.photos)
var imgArr = result.data.data.photos.split(",");
for(var i=0;i<imgArr.length;i++){
if(imgArr[i].indexOf("http") == -1) {

imgArr[i] = "http://" + imgArr[i]
}
}
_this.imgArr=imgArr

})


},
methods:{
back(){
this.$router.go(-1);
this.$router.isBack = true;
},
successLoad(src,ele){
const img = ele.parentNode.querySelector('img')
ele.parentNode.removeChild(img)
}
}

}



