
import Search from 'vux/src/components/search/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

const list = () => ['本科','专科']
export default{
name:"SelectPro",
components:{
Search,
Tab,
TabItem,
Swiper,
SwiperItem
},
data(){
return {
results: [],
value: this.$store.state.ProSearch,
index:0,
demo2: '本科',
list2: list(),
ProArr:['哲学','经济学','法学','教育学','文学','历史学','理学','工学'],
classifyArr:["哲学 (4)","哲学 (4)","哲学 (4)"],
moreArr:['经济学','经济学','经济学','经济学'],
moreFlag:[]
}
},
mounted(){
$(".vux-search-box").css("width","5.8rem")
this.$nextTick(function() {
var height = Math.round(0.9 * this.ProArr.length) + 0.5 + "rem"
this.$refs.swiper.xheight = height
})
$(".course").eq(0).css({"background":"#2B94FA","color":"#ffffff"});

for(var i=0;i<this.classifyArr.length;i++){
this.moreFlag[i]=true
}
console.log(this.moreFlag)
},
methods:{
back() {
this.$router.go(-1);
this.$router.isBack = true;
},
getResult(val) {
console.log(val)

},
onFocus() {
console.log('on focus')
},
onCancel() {
console.log('on cancel')
},
onBlur() {
console.log(this.value)
this.$store.state.ProSearch=this.value
this.$router.push({
path: '/SearchPro'
})
},
change(){

},
changePro(index){
$(".course").eq(index).css({"background":"#2B94FA","color":"#ffffff"}).siblings(".course").css({"background":"#FFFFFF","color":"#1B1E1E"});


},
showMore(index){
if(this.moreFlag[index]){
$('.classify').eq(index).children(".icon").animate({"transform":"rotate(90deg)"})
$('.classify').eq(index).animate({"height":0.88+this.moreArr.length*0.8+"rem"})
this.moreFlag[index]=false
}else{
$('.classify').eq(index).children(".icon").animate({"transform":"rotate(0deg)"})
$('.classify').eq(index).animate({"height":0.88+"rem"})
this.moreFlag[index]=true
}

}
}
}



