

var height=document.documentElement.clientHeight*2/100+"rem";
import XHeader from 'vux/src/components/x-header/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
directives: {
TransferDom
},
name:"Wish",
components:{
XHeader,
Picker,
Cell,
Toast,
Confirm,
Loading

},
data:function(){
return{
school:this.message.school,
sex:this.message.sex,

sexData:[['不限', '男生较多', '女生较多', '男女平均']],
schoolData:[['广东', '北京', '上海', '天津']],
maskStyle:{
height:height
},
transfer:{

sex:[""],
school:[""]
},
ProLen:"已选"+this.message.pro.length+"个专业",
OccLen:"已选"+this.message.occ.length+"个方向",
showLoad:false
}
},
mounted(){
this.sexWidth();
this.changeDiable();
if(this.message.school==""){
console.log()
this.transfer.school[0]=this.schoolData[0][0]
}else{
this.transfer.school[0]=this.message.school
}
if(this.message.sex==""){
this.transfer.sex[0]=this.sexData[0][0]
}else{
this.transfer.sex[0]=this.message.sex
}


},


methods:{
back(){
this.$router.go(-1);
this.$router.isBack = true;
},
comfirm(){
$(".weui-toast").css("width","2.5rem")
$(".weui-toast").css("min-height","2.5rem")
this.showLoad=true;
var _this=this
setTimeout(function(){
_this.showLoad=false;
_this.$router.push({path:'/Analog'})
},800)



},
goPro(){
this.$router.push({path:'/Professional'})
},
goOcc(){
this.$router.push({path:'/Occupation'})
},
schoolShow(){
$("#school").fadeIn()
},
closeSchool(){
$("#school").fadeOut()
},
comfirmSchool(){
this.message.school=this.transfer.school[0];
this.school=this.message.school;
$("#school").fadeOut()
this.changeDiable()
},
schoolChange(value){
this.transfer.school[0]=value[0];
},
sexShow(){
$("#sex").fadeIn()
},
closeSex(){
$("#sex").fadeOut()
},
comfirmSex(){
this.message.sex=this.transfer.sex[0];
this.sex=this.message.sex;
this.sexWidth()
$("#sex").fadeOut()
this.changeDiable()
},
sexChange(value){

this.transfer.sex[0]=value[0];
},
sexWidth(){
if(this.sex=="不限"){
$(".sexBox .select").css("width","1rem")
}else{
$(".sexBox .select").css("width","1.5rem")
}
}
,
changeDiable(){
if(this.school!=""&&this.sex!=""&&this.message.pro.length!=0&&this.message.occ.length!=0){
$('.confirm_btn').removeAttr("disabled");
$('.confirm_btn').css("background","#2B94FA")
}
}
}
}



