import Swiper from '../../static/swiper.min.js'
let galleryThumbs
export default {
	name: 'Home',
	data() {
		return {
			transitionName: 'slide-left',
			mark: this.message.mark,
			subject: this.message.subject,
			fromPlace: this.message.fromPlace,
			goTop: false,
			list: [],
			
			
		}
	},
	mounted() {
		this.lunbo();
		window.addEventListener('scroll', this.handleScroll);
		var _this = this;
		this.$http.get("https://www.xueguoguo.cn/wxapi/Article?funcid=list&id=4", {}).then(function(result) {
			console.log(result.data)
			_this.list = result.data.list
		});
		for(var i = 0; i < this.list.length; i++) {
			var index = this.list[i].createtime.indexOf(" ")
			this.list[i].createtime.substr()
		}
	},
	methods: {
		goSchoolSelect() {
			this.$router.push({
				path: '/SchoolSelect'
			})
		},
		goSelectPro(){
			this.$router.push({
				path: '/SelectPro'
			})
		},
		lunbo() {
			galleryThumbs = new Swiper('.gallery-thumbs', {

				initialSlide: 0,
				centeredSlides: false,
				slidesPerView: "auto",
				touchRatio: 0.3,
				slideToClickedSlide: true,
				autoplayDisableOnInteraction: true,
				coverflow: {
					rotate: 0,
					stretch: 0,
					depth: 100,
					modifier: 1,
					lideShadows: false
				},

			})
		},
		stopPlay() {
			galleryThumbs.stopAutoplay()
		},
		play() {
			galleryThumbs.startAutoplay()
		},

		handleScroll(e) {
             
			console.log(1)
			var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
			var offsetTop = document.querySelector('.list-first').offsetTop;
			if(scrollTop > offsetTop) {
				this.goTop = true
			} else {
				this.goTop = false
			}
		},
		goToTop(e) {

			var  timer = setInterval(function() {
				var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
				var speed = scrollTop / 5;
				if(document.body.scrollTop) {
					document.body.scrollTop -= speed;
				} else {
					document.documentElement.scrollTop -= speed;
				}
				if(scrollTop == 0) {
					clearInterval(timer);
				}
			}, 30)

		},
		goEach(id) {
			this.$store.state.listId=id
			console.log(this.$store.state.listId)
			this.$router.push({
				path: '/Information'
			})
		}
	},
	destroyed() {
		document.documentElement.scrollTop=0
		window.removeEventListener("scroll", this.handleScroll)
	}
}



